var moment = require('moment');
  require("moment-duration-format");

  export default {
    props: ['album'],

    methods: {
      playSong: function(song) {
        this.$dispatch('playSong', song);
      },
      getTime: function(song) {
        let time = moment.duration(song.duration, 'seconds');
        return time.format('h:m:ss');
      }
    }
  }


