export default {
  props: ['album'],

  data: function() {
    return {
      albumArt: {}
    };
  },

  created: function () {
    this.getAlbumArt(this.album.coverArt);
  },

  methods: {
    getAlbumArt: function(id) {
      if (id === undefined) {
        return false;
      }

      this.$dispatch('getAlbumArt', id);
    }
  },

  events: {
    getAlbumArt: function(albumArt) {
      if (this.album.coverArt == albumArt.id) {
        this.albumArt = albumArt;
      }
    }
  }
};


