import Artist from './Artist.vue'

export default {
  props: ['artists'],

  data: function() {
    return {
      active: 0
    }
  },

  methods: {
    getArtist: function(id) {
      this.$dispatch('getArtist', id);
    }
  },

  events: {
    activeArtistChanged: function(id) {
      this.active = id;
    }
  },

  components: {
    artist: Artist
  }
};


