export default {
  props: ['artist', 'active'],

  methods: {
    getArtist: function(id) {
      this.$dispatch('getArtist', id);
      this.$dispatch('activeArtistChanged', this.artist.id);
      this.$dispatch('setState', 'artist')
    },
  }
};


